import React from "react";
import { connect } from "react-redux";
import { Line } from "react-chartjs-2";
import moment from "moment";

const ProgressionByCountry = ({ fromDayOne }) => {
	const data = {
		labels: fromDayOne?.map((country) => moment(country.Date).format("MMM Do")),
		datasets: [
			{
				label: "Confirmed Cases",
				data: fromDayOne?.map((country) => country.Confirmed),
				backgroundColor: [
					"rgba(54, 162, 235, 0.2)",
					"rgba(255, 206, 86, 0.2)",
					"rgba(75, 192, 192, 0.2)",
					"rgba(153, 102, 255, 0.2)",
					"rgba(255, 159, 64, 0.2)",
				],
				borderColor: [
					"rgba(255, 99, 132, 1)",
					"rgba(54, 162, 235, 1)",
					"rgba(255, 206, 86, 1)",
					"rgba(75, 192, 192, 1)",
					"rgba(153, 102, 255, 1)",
					"rgba(255, 159, 64, 1)",
				],
				borderWidth: 1,
			},
			{
				label: "Deaths",
				data: fromDayOne?.map((country) => country.Deaths),
				backgroundColor: [
					"rgba(255, 206, 86, 0.2)",
					"rgba(75, 192, 192, 0.2)",
					"rgba(153, 102, 255, 0.2)",
					"rgba(255, 159, 64, 0.2)",
				],
				borderColor: [
					"rgba(255, 99, 132, 1)",
					"rgba(54, 162, 235, 1)",
					"rgba(255, 206, 86, 1)",
					"rgba(75, 192, 192, 1)",
					"rgba(153, 102, 255, 1)",
					"rgba(255, 159, 64, 1)",
				],
				borderWidth: 1,
			},
			{
				label: "Recovered",
				data: fromDayOne?.map((country) => country.Recovered),
				backgroundColor: [
					"rgba(75, 192, 192, 0.2)",
					"rgba(153, 102, 255, 0.2)",
					"rgba(255, 159, 64, 0.2)",
				],
				borderColor: [
					"rgba(255, 99, 132, 1)",
					"rgba(54, 162, 235, 1)",
					"rgba(255, 206, 86, 1)",
					"rgba(75, 192, 192, 1)",
					"rgba(153, 102, 255, 1)",
					"rgba(255, 159, 64, 1)",
				],
				borderWidth: 1,
			},
		],
	};
	return (
		<div className='custom-m-3 has-text-centered'>
			<small>Click on parameters to select or unselect them</small>
			{fromDayOne ? <Line options={{ responsive: true }} data={data} /> : null}
		</div>
	);
};

const mapStateToProps = ({ fromDayOne }) => {
	return { fromDayOne };
};

export default connect(mapStateToProps)(ProgressionByCountry);
